import React, { useEffect, useState } from 'react';

const CommentList = () => {
  const [newComments, setnewComments] = useState([])

  const fetchcomment = async () => { 
    fetch('https://60d422e261160900173ca596.mockapi.io/commentpost')
    .then(res => res.json())
    .then((data) => {
     setnewComments(data)
    })
    }

    useEffect(() => {
    fetchcomment()
     }, []);

    return (
      <tbody>
        
                     {
                     
                      newComments.map((comment)=>{
                        return (
                        <tr key={comment.id}>
                          
                          <td>{comment.commentdata}</td>
                       
                         
                        </tr>
                        )
                      }) 
                     }
                  </tbody>
);  
}

export default CommentList

