import { DELETEBOOK, SUBMITBOOKDATA, UPDATEBOOK } from '../../constants'

const initalState = []

export const NewBookReducer = (state = initalState, action) => {
    switch (action.type) {
        case SUBMITBOOKDATA: {
        
            state.push(action.payload)

            console.log(state)
            return [...state]
        }
        
        case DELETEBOOK: {
            fetch(`https://60a83eeb8532520017ae5bd9.mockapi.io/bookapp/${action.payload}`, {
                method: "DELETE",
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/json'
                }
            })

            alert("Book is deleted from the List")

            return [...state]
        }
        
        case UPDATEBOOK: {
            fetch(`https://60a83eeb8532520017ae5bd9.mockapi.io/bookapp/${action.payload.id}`, {
                method: "PUT",
                 headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/json'
                },
                 body: JSON.stringify(action.payload.newBookData)
            })

            return [...state]
        }

        
        default: return [...state]
    }

}
export const updateBookDetail = () => async (dispatch, getState) => {
    
    const thedetails = getState()
    const data1 = thedetails[thedetails.length - 1]
    await fetch("https://60a83eeb8532520017ae5bd9.mockapi.io/bookapp", {
    method: "POST",
    headers: {
      'Accept': 'application/json',
      'Content-type': 'application/json'
        },
    body: JSON.stringify(data1)
         })
    alert("Match Added Successfully to List")
}