import React from 'react';
import {Link } from 'react-router-dom'

const Services = () => {

    let emailname, password, entername, age, mobilenumber;



    const loginHandler = () => {
            
        
            let emailvalid =
              /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        
            if (emailvalid.test(emailname.value)) {
              if (password.value.length >= 8) {
        alert("We will reply you soon!!")
        window.location = "/services";
              
              } else {
                alert('Password should not be empty (atleast 8 characters)');
              }
            } else {
              alert('Please enter valid email id!!');
            }
          };


        return (
          <div>
          <div class="ui inverted segment">
            
  <div class="ui inverted secondary menu">
  <a class="active item">
  
  <Link to="/"><h2>Esports Arena</h2></Link>
  </a>
    <a class="item">
    <Link to="/login">Login</Link>
    </a>
    <a class="item">
    <Link to="/gamingpost"> Gaming</Link>
    </a>
    <a class="item">
      Chat Social
    </a>
    <a class="item">
    <Link to="/booklist">Add Game Match</Link>
    </a>
    <a class="item">
    <Link to="/showdata"> Search Players</Link>
    </a>
    <a class="item">
      Buy/Sell Game Product
    </a>
    <a class="item">
    <Link to="/services"> Services</Link>
    </a>
  </div>
</div>
            <div className='col-lg-6 col-md-6 overflow-hidden m-auto'>
    
            
            <h1 className='my-3 text-info'>How to Contact Us? </h1>
               <div className="form-group">
                 <label htmlFor="title">Enter Name:</label>
                 <input type="text"className="form-control" ref = {(input) => entername = input} placeholder="Enter Name" />
               
               </div>
               <div className="form-group">
                 <label htmlFor="title">Mobile No:</label>
                 <input type="text"className="form-control" ref = {(input) => mobilenumber = input} placeholder="Enter Number" />
               
               </div>
               <div className="form-group">
                 <label htmlFor="title">Enter Email:</label>
                 <input type="text"className="form-control" ref = {(input) => emailname = input} placeholder="Enter Email" />
               
               </div>
               <div className="form-group">
                 <label htmlFor="password">Descriobe your Issue?</label>
                 <input type="text"  className="form-control" ref = {(input) => password = input} placeholder="Description" />
               </div>
           {
            
               <button className="btn btn-dark btn-block" onClick={() => { loginHandler() }}>Submit Form</button>   
              
          
             
           }
            
             </div>
             </div>
        );
}

export default Services;