import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { deleteBook, updateBook } from './Actions/bookactions'
import {Link } from 'react-router-dom'


const BookList = () => {
  const [nstate, setnstate] = useState(false);
  const dispatch = useDispatch()
  const [book, setBook] = useState({})
  const [newBooks, setnewBooks] = useState([])

  const fetchBook = async () => { 
    fetch('https://60a83eeb8532520017ae5bd9.mockapi.io/bookapp')
    .then(res => res.json())
    .then((data) => {
     setnewBooks(data)
    })
    }

    useEffect(() => {
    fetchBook()
     }, []);

     const editbookHandler = (id) => {
      setnstate(true)
      const thenewid = newBooks.find((el) => {
          return el.id === id
      })
      setBook(thenewid) 
  }

  
    const submitHandler = () => {
      
        let NewPrice = parseInt(newprice.value)
        let NewRelease = parseInt(newrelease.value)

        if (!newname.value || !NewPrice || !newauthorname.value || !NewRelease || !newdescription.value || !newimageurl ) {
            alert("Do not leave any field empty!!")
        }
        else if (NewPrice > 0 && NewPrice < 700) {
            if (NewRelease > 1995 && NewRelease < 2021) {
                      const newBookData = {
                            bookname: newname.value,
                            price: NewPrice,
                            authorname: newauthorname.value,
                            releaseyear: NewRelease,
                            description: newdescription.value,
                            imgurl: newimageurl.value
                        
                        }
                        dispatch(updateBook(book.id, newBookData))
                        setnstate(false)
                        alert("The Book successfully updated.")
                        fetchBook()
            }
            else {
                alert("Books must be published after 1995 and before 2021")
            }
        } else {
            alert("Maximum price should not exceed 800")
          
        }
       }

       const deleteBookHandler = (id) => {
        dispatch(deleteBook(id))
        window.location = "/booklist";
    }

    let newname, newprice, newauthorname, newrelease, newdescription, newimageurl;
    return (
      <div>
      <div class="ui inverted segment">
        
<div class="ui inverted secondary menu">
<a class="active item">

<Link to="/"><h2>Esports Arena</h2></Link>
</a>
<a class="item">
<Link to="/signup"> Signup</Link>
</a>
<a class="item">
<Link to="/gamingpost"> Gaming</Link>
</a>
<a class="item">
  Chat Social
</a>
<a class="item">
<Link to="/gamingpost"> Add Game Match</Link>
</a>
<a class="item">
<Link to="/showdata"> Search Players</Link>
</a>
<a class="item">
  Buy/Sell Game Product
</a>
<a class="item">
<Link to="/services"> Services</Link>
</a>
</div>
</div>

            <div className="container ">
            <div className='mt-4 '>
            </div>
            <div className='row main'>
            
        {nstate === false ? <>
              <div className='col-lg-6 col-md-6 overflow-hidden m-auto'>
            
             <h1 className='my-3 text-info'>Add and Organize Games</h1>
               <p>1) You can add the game match</p>
               <p>2) You Can edit game match</p>
               <p>3) Delete the Game schedule</p>
            {
              <Link to="/addbook"><button className="btn btn-dark">Add Match</button>   </Link>
            }
             
              </div>
              </> : <>
              <div className='col-lg-6 col-md-6 overflow-hidden m-auto'>
            
            <h1 className='my-3 text-info'>Edit the Match Details </h1>
               <div className="form-group">
                 <label htmlFor="title">Enter Game Name</label>
                 <input type="text"className="form-control" ref = {(input) => newname = input} placeholder="Enter Game Name" />
               
               </div>
               <div className="form-group">
                 <label htmlFor="price">Enter Players</label>
                 <input type="number"  className="form-control" ref = {(input) => newprice = input} placeholder="Enter Players" />
               </div>
               <div className="form-group">
                 <label htmlFor="author">Organizer Name</label>
                 <input type="text"  className="form-control"  ref = {(input) => newauthorname = input} placeholder="Enter Organizer Name" />
               </div>
               <div className="form-group">
                 <label htmlFor="release">Total Matches</label>
                 <input type="number" className="form-control" ref = {(input) => newrelease = input} placeholder="Total Matches" />
               </div>
               <div className="form-group">
                 <label htmlFor="newdesc">Description</label>
                 <input type="text"   className="form-control"  ref = {(input) => newdescription = input} placeholder="Description" />
               </div>
               <div className="form-group">
                 <label htmlFor="newurl">Image URL</label>
                 <input type="string"  className="form-control" ref = {(input) => newimageurl = input} placeholder="Image URL" />
               </div>
           
             <button className="btn btn-dark btn-block" onClick={() => { submitHandler() }}>Update Game Details</button>  
             <br/>
             <Link to="/addbook"><button className="btn btn-dark ">Add Match</button>   </Link>
           
            
             </div>
                
            </>}
        
              <div className='col-md-6 col-lg-6 col-12 m-auto '>
                {
        
                <table className="table table-responsive w-100">
                  <thead>
                    <tr>
                      <th scope="col">Game Name</th>
                      <th scope="col">Max Participants</th>
                      <th scope="col">Organizer</th>
                      <th scope="col">Operations</th>
                    </tr>
                  </thead>
                  <tbody>
        
                     {
                     
                      newBooks.map((book)=>{
                        return (
                        <tr key={book.id}>
                          
                          <td>{book.bookname}</td>
                          <td>{book.price}</td>
                          <td>{book.authorname}</td>
                          <td className='d-flex'>
                            <button className='btn btn-info m-2'onClick={() => { editbookHandler(book.id) }}>Edit</button>
                            <button className='btn btn-danger m-2' onClick={() => { deleteBookHandler(book.id) }}>Delete</button>
                          </td>
                        </tr>
                        )
                      }) 
                     }
                  </tbody>
                </table>
                }
                </div>
        
            </div>
            
              
             </div>
           
</div>
       
          )

}

export default BookList

