import { SUBMITBOOKDATA, DELETEBOOK, UPDATEBOOK } from '../../constants'


export const updateBook = (id, newBookData) => {
    return {
        type: UPDATEBOOK,
        payload: {id, newBookData}
    }
}

export const deleteBook = (id) => {
    return {
        type: DELETEBOOK,
        payload: id
    };
}

export const submitBookData = (data) => {
    return {
        type: SUBMITBOOKDATA,
        payload: data
    }
}
