import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { postthecomment } from './Actions/commentaction'
import { addthedetail } from './Reducers/commentreducer'



const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
      width: '35ch',
    },
  },
}));


export default function BasicTextFields() {
  const classes = useStyles();
  
  const dispatch = useDispatch();

  const[content,setcontent] = useState("");

  const submitHandler=(event)=>{
      const data={commentdata:content}
      dispatch(postthecomment(data));
      dispatch(addthedetail());
      setcontent("");
  };

  return (
      <div>
    <form className={classes.root} noValidate autoComplete="off">
      <TextField id="standard-basic" label="Comment" placeholder="Write comment here"
      value={content}
      onChange={(e)=>setcontent(e.target.value)} />
      <Button variant="contained" onClick={() => { submitHandler() }}>Post</Button>
      

    </form>
    </div>
  );
}
