import React from 'react'
import { useDispatch } from 'react-redux'
import { submitBookData } from './Actions/bookactions'
import { updateBookDetail } from './Reducers/bookreducer'
import {Link } from 'react-router-dom'


const AddBook = () => {

    let bookname, price, authorname, release, description, imageurl
    const dispatch = useDispatch();

    const submitHandler = () => {
    

        let Price = parseInt(price.value)
        let Release = parseInt(release.value)

        if (!bookname.value || !Price || !authorname.value || !Release || !description.value || !imageurl ) {
            alert("All Fields must be filled!!");
        }

        else if (Price > 20 && Price < 200) {
            if (Release > 0 && Release < 10) {
                        const data = {
                    bookname: bookname.value,
                    price: Price,
                    authorname: authorname.value,
                    releaseyear: Release,
                    description: description.value,
                    imgurl: imageurl.value
                    
                }

                dispatch(submitBookData(data));
                dispatch(updateBookDetail())
                bookname.value = ''
                price.value = ''
                authorname.value = ''
                release.value = ''
                description.value = ''
                imageurl.value = ''
            }
            else {
                alert("Minimum 20 players and maximum 200 players")
            }
        } else {
            alert("Maximum matches: 10")
            
        }       
    }

    return (
      <div>
      <div class="ui inverted segment">
        
      <div class="ui inverted secondary menu">
      <a class="active item">
      
      <Link to="/"><h2>Esports Arena</h2></Link>
      </a>
      <a class="item">
      <Link to="/signup"> Signup</Link>
      </a>
      <a class="item">
      <Link to="/gamingpost"> Gaming</Link>
      </a>
      <a class="item">
        Chat Social
      </a>
      <a class="item">
      <Link to="/gamingpost"> Add Game Match</Link>
      </a>
      <a class="item">
      <Link to="/showdata"> Search Players</Link>
      </a>
      <a class="item">
        Buy/Sell Game Product
      </a>
      <a class="item">
      <Link to="/services"> Services</Link>
      </a>
      </div>
      </div>
        <div className='col-lg-6 col-md-6 overflow-hidden m-auto'>
            
            <h1 className='my-3 text-info'>Add and organize Game </h1>
               <div className="form-group">
                 <label htmlFor="title">Enter Game Name</label>
                 <input type="text"className="form-control" ref = {(input) => bookname = input} placeholder="Enter Game Title" />
               
               </div>
               <div className="form-group">
                 <label htmlFor="price">Total Players</label>
                 <input type="number"  className="form-control" ref = {(input) => price = input} placeholder="Total Players" />
               </div>
               <div className="form-group">
                 <label htmlFor="author">Organizer Name</label>
                 <input type="text"  className="form-control"  ref = {(input) => authorname = input} placeholder="Organizer Name" />
               </div>
               <div className="form-group">
                 <label htmlFor="release">Total Matches</label>
                 <input type="number" className="form-control" id="release" ref = {(input) => release = input} placeholder="Total Matches" />
               </div>
               <div className="form-group">
                 <label htmlFor="desc">Description</label>
                 <input type="text"   className="form-control"  ref = {(input) => description = input} placeholder="Description" />
               </div>
               <div className="form-group">
                 <label htmlFor="url">Image URL</label>
                 <input type="string"  className="form-control" ref = {(input) => imageurl = input} placeholder="Image URL" />
               </div>
           
               <button className="btn btn-dark btn-block" onClick={() => { submitHandler() }}>Add Match</button>   
               <br/>
               <Link to="/booklist"><button className="btn btn-dark ">Go Back to Matches List</button>  </Link>
  
             </div>
             </div>
    )
}

export default AddBook

