import {POSTCOMMENT } from '../../constants'

const initalState = []

export const NewCommentReducer = (state2 = initalState, action) => {
    switch (action.type) {
        case POSTCOMMENT: {
        
            state2.push(action.payload)

            console.log(state2)
            return [...state2]
        }

        
        default: return [...state2]
    }

}
export const addthedetail = () => async (dispatch, getState) => {
    
    const thedetails2 = getState()
    const data2 = thedetails2[thedetails2.length - 1]
    await fetch("https://60d422e261160900173ca596.mockapi.io/commentpost", {
    method: "POST",
    headers: {
      'Accept': 'application/json',
      'Content-type': 'application/json'
        },
    body: JSON.stringify(data2)
         })
    alert("Comment Added")
}