import React from 'react';
import {Link } from 'react-router-dom'

const Login = () => {

    let emailname, password;

    const postrequest2 = async() => {

        const logindata = {
            email: emailname.value,
            password: password.value
        }
        fetch("https://reqres.in/api/login", {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify(logindata)
          })
            .then(res => {
              if (res.ok) {
            alert("You are successfully authenticated!");
                window.location = "/booklist";
              } else {
                throw Error(res.statusText);
              }
            })
            .catch(error => {
                console.error(error);
                alert("You are attempting wrong credentials!!");
                window.location = "/";
           
            });

    }


    const loginHandler = () => {
            
        
            let emailvalid =
              /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        
            if (emailvalid.test(emailname.value)) {
              if (password.value.length >= 8) {
        
                postrequest2();
              } else {
                alert('Password should not be empty (atleast 8 characters)');
              }
            } else {
              alert('Please enter valid email id!!');
            }
          };


        return (
          <div>
          <div class="ui inverted segment">
            
  <div class="ui inverted secondary menu">
  <a class="active item">
  
  <Link to="/"><h2>Esports Arena</h2></Link>
  </a>
    <a class="item">
    <Link to="/signup"> Signup</Link>
    </a>
    <a class="item">
    <Link to="/gamingpost"> Gaming</Link>
    </a>
    <a class="item">
      Chat Social
    </a>
    <a class="item">
    <Link to="/booklist"> Add Game Match</Link>
    </a>
    <a class="item">
    <Link to="/showdata"> Search Players</Link>
    </a>
    <a class="item">
      Buy/Sell Game Product
    </a>
    <a class="item">
    <Link to="/services"> Services</Link>
    </a>
  </div>
</div>
            <div className='col-lg-6 col-md-6 overflow-hidden m-auto'>
    
            
            <h1 className='my-3 text-info'>WELCOME to ESports Arena </h1>
            <h2>Login</h2>
               <div className="form-group">
                 <label htmlFor="title">Enter Email:</label>
                 <input type="text"className="form-control" ref = {(input) => emailname = input} placeholder="Enter Email" />
               
               </div>
               <div className="form-group">
                 <label htmlFor="password">Enter Password</label>
                 <input type="password"  className="form-control" ref = {(input) => password = input} placeholder="Password" />
               </div>
           {
             <div>
               <button className="btn btn-dark btn-block" onClick={() => { loginHandler() }}>Login </button>  
               <br/>
               <p>Don't have an account, Signup below:</p>

               <Link to="/signup"><button className="btn btn-dark">Signup</button>   </Link>
               </div>
           }
            
             </div>
             </div>
        );
}

export default Login;