import React, { Component } from "react";
import Details from "./Details";
import { Link } from 'react-router-dom';


class Showdata extends Component {

    constructor() {
        super();

        this.state = {
            search: null
        };
    }

    onSearch = (event) => {
        let searchterm = event.target.value;
        this.setState({ search: searchterm })
    }
    

    render() {
        
        const items = Details.filter((data) => {
            if (this.state.search == null)
                return data
            else if (data.name.toLowerCase().includes(this.state.search.toLowerCase()) || data.course.toLowerCase().includes(this.state.search.toLowerCase())) {
                return data
            }
        }).map(data => {
            return (
                <div className="ui container">

                    <table className="ui fixed table" >

                        <tbody>
                            <tr>
                                <td>{data.name}</td>
                                <td>{data.age}</td>
                                <td>{data.gender}</td>
                                <td>{data.course}</td>
                            </tr>
                        </tbody>
                    </table>
                    </div>
            )
        })

        return (
        <div>
        <div class="ui inverted segment">
            
        <div class="ui inverted secondary menu">
        <a class="active item">
        
        <h2>Esports Arena</h2>
        </a>
          <a class="item">
          <Link to="/signup"> Signup</Link>
          </a>
          <a class="item">
          <Link to="/gamingpost"> Gaming</Link>
          </a>
          <a class="item">
            Chat Social
          </a>
          <a class="item">
          <Link to="/booklist"> Add Game Match</Link>
          </a>
          <a class="item">
          <Link to="/showdata"> Search Players</Link>
          </a>
          <a class="item">
            Buy/Sell Game Product
          </a>
          <a class="item">
          <Link to="/services"> Services</Link>
          </a>
        </div>
      </div>
            <div className="ui container" style={{ marginTop: '10px' }}>
            <div className="ui form">

                <div>
                <div className="ui segment">
                <form className="ui form">
                <div className="field">
                <label><h2>Search Players</h2></label>
                        <input type="text" placeholder="Enter the course or Name" onChange={(e) => this.onSearch(e)} />
                        </div>
                        <Link to="/"> <button className="ui button">
                            Go Back
                       </button> </Link>
                    </form>
                    </div>
                    <table className="ui fixed table">
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Age</th>
                                <th>Game</th>
                                <th>Role</th>
                            </tr>
                        </thead>
                    </table>
                    {items}
                </div>

            </div>
            </div>
            </div>
        )
    }
}

export default Showdata;